.b-tabs {
  margin-bottom: 2rem;
}

.tabs {
  ul {
    border-bottom: none;
  
    li {
      position: relative;

      &:before {
        content: '';
        position: absolute;
        background: #252A32;
        border-radius: 2px;
        top: 4px;
        bottom: 4px;
        left: 4px;
        right: 4px;
        z-index: 0;
      }

      a {
        position: relative;
        z-index: 1;
        border: none;

        color: #6B7381;
        letter-spacing: 0.02em;
        font-weight: bold;

        border-top: 1px solid transparent;
        border-image-slice: 1;
        border-width: 2px;
        border-image-source: none;
      }

      &:hover a {
        color: #fff;
      }

      &.is-active a {
        border-color: 1px solid #1fba7c;
        border-image-source: linear-gradient(to left, #1fba7c, #0d95e1);

        background: #1E2329;
        color: #fff;
      }
    }
  }
}

.Wallet .tabs li:last-child:not(:first-child) {
  margin-left: auto;
}

.tab-content {
  background: #1E2329;
  padding: 0 1rem;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.1), 0px 2px 6px rgba(0, 0, 0, 0.1), 0px 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 2px 2px 3.125rem 2px;

  .tab-item {
    padding: 1.5rem 0;
  }
}