































































































.socials {
  align-items: center;
}
.in-footer {
  background-color: #11151b;

  .column {
    border-top: 1px solid #000;
    border-right: 1px solid #000;
    box-sizing: border-box;

    .extra-small {
      margin: 0.5rem auto;
    }

    &:hover {
      background-color: rgba(255, 255, 255, 0.02);
    }
    &:last-child {
      border-right: 0;
    }
  }
}
.in-header {
  .column {
    margin: 0 0.45rem;
    padding: 0 !important;
  }
  a:hover {
    opacity: 0.85;
  }
}
