$floating-in-height: 3.25em !default;

.field {
    &.is-grouped {
        .field {
            flex-shrink: 0;
            &:not(:last-child) {
                margin-right: 0.75rem;
            }
            &.is-expanded {
                flex-grow: 1;
                flex-shrink: 1;
            }
        }
    }
    // Nested control addons (for Autocomplete and Datepicker)
    &.has-addons .control {
        &:first-child .control {
            .button,
            .input,
            .select select {
                border-bottom-left-radius: $input-radius;
                border-top-left-radius: $input-radius;
            }
        }
        &:last-child .control {
            .button,
            .input,
            .select select {
                border-bottom-right-radius: $input-radius;
                border-top-right-radius: $input-radius;
            }
        }
        .control {
            .button,
            .input,
            .select select {
                border-radius: 0
            }
        }
    }
    // Fix for number input with addons
    &.has-addons {
        .b-numberinput {
            &:not(:first-child) {
                .control:first-child {
                    .button,
                    .input,
                    .select select {
                        border-bottom-left-radius: 0;
                        border-top-left-radius: 0;
                    }
                }
            }
            &:not(:last-child) {
                .control:last-child {
                    .button,
                    .input,
                    .select select {
                        border-bottom-right-radius: 0;
                        border-top-right-radius: 0;
                    }
                }
            }
        }
    }
}

.field {
    &.is-floating-label,
    &.is-floating-in-label {
        position: relative;

        .label {
            position: absolute;
            left: 1em;
            font-size: calc(#{$size-normal} * 3 / 4);
            background-color: transparent;
            z-index: 5;
            &.is-small {
                font-size: calc(#{$size-small} * 3 / 4);
            }
            &.is-medium {
                font-size: calc(#{$size-medium} * 3 / 4);
            }
            &.is-large {
                font-size: calc(#{$size-large} * 3 / 4);
            }
        }
        .taginput .counter {
            float: none;
            text-align: right;
        }

        &.has-addons {
            > .label {
                + .control {
                    .button,
                    .input,
                    .select select {
                        border-bottom-left-radius: $input-radius;
                        border-top-left-radius: $input-radius;
                    }
                }
            }
        }
    }

    &.is-floating-label {
        .label {
            top: -0.775em;
            padding-left: 0.125em;
            padding-right: 0.125em;
            &:before {
                content: '';
                display: block;
                position: absolute;
                top: 0.775em;
                left: 0;
                right: 0;
                height: 0.375em;
                background-color: $input-background-color;
                z-index: -1;
            }
        }
        .input,
        .textarea,
        .select
        select {
            &:focus {
                box-shadow: none;
            }
        }
        .taginput .taginput-container {
            padding-top: 0.475em;
            &.is-focused {
                box-shadow: none;
            }
        }
    }
    &.is-floating-in-label {
        .label {
            top: 0.25em;
        }
        > .datepicker, > .timepicker {
            .input {
                padding-top: calc(#{$floating-in-height} / 2 - (#{$size-large} * 3 / 4) / 2);
                padding-bottom: 1px;
                height: $floating-in-height;
            }
        }
        > :not(.datepicker):not(.timepicker):not(.taginput) {
            .input,
            .textarea,
            select {
                padding-top: calc(#{$floating-in-height} / 2 - (#{$size-large} * 3 / 4) / 2);
                padding-bottom: 1px;
                height: $floating-in-height;
            }
            .select:not(multiple) {
                height: $floating-in-height;
                &.is-loading {
                    &::after {
                        margin-top: calc(#{$floating-in-height} / 2 - (#{$size-large} * 3 / 4) / 2);
                    }
                }
                &::after {
                    margin-top: 1px;
                }
            }
        }
        > :not(.taginput) {
            .is-left.icon,
            .is-right.icon {
                height: $floating-in-height;
            }
            .is-left.icon {
                padding-top: calc(#{$floating-in-height} / 2 - (#{$size-large} * 3 / 4) / 2);
            }
        }
        .control.is-loading::after {
            margin-top: calc(#{$floating-in-height} / 2 - (#{$size-large} * 3 / 4) / 2);
        }
        .taginput .taginput-container {
            // 0.275em - 1px _taginput.scss
            padding-top: calc(#{$floating-in-height} / 2 - (#{$size-large} * 3 / 4) / 2 + (0.275em - 1px));
        }

        &.has-numberinput {
            .b-numberinput {
                .control {
                    .button {
                        height: $floating-in-height;
                    }
                }
            }
        }

        &.has-addons, &.is-grouped {
            .control {
                .button,
                .input,
                .select select {
                    height: $floating-in-height
                }
            }
        }
    }
    &.is-floating-label, &.is-floating-in-label {
        &.has-numberinput {
            .label {
                margin-left: calc(#{$size-normal} * 3);
            }
            &.has-numberinput-is-small {
                .label {
                    margin-left: calc(#{$size-small} * 3);
                }
            }
            &.has-numberinput-is-medium {
                .label {
                    margin-left: calc(#{$size-medium} * 3);
                }
            }
            &.has-numberinput-is-large {
                .label {
                    margin-left: calc(#{$size-large} * 3);
                }
            }
        }
        &.has-numberinput-compact {
            .label {
                margin-left: calc(#{$size-normal} * 2.25);
            }
            &.has-numberinput-is-small {
                .label {
                    margin-left: calc(#{$size-small} * 2.25);
                }
            }
            &.has-numberinput-is-medium {
                .label {
                    margin-left: calc(#{$size-medium} * 2.25);
                }
            }
            &.has-numberinput-is-large {
                .label {
                    margin-left: calc(#{$size-large} * 2.25);
                }
            }
        }
    }
    &.is-grouped-right, &.has-addons-right {
        &.is-floating-in-label {
            .label {
                position: relative;
                left: calc(3.25em + 2em);
            }
        }
        &.is-floating-label {
            .label {
                position: relative;
                left: calc(3.25em + 2em);
            }
        }
    }
}

.control {
    .help.counter {
        float: right;
        margin-left: 0.5em;
    }
    .icon {
        &.is-clickable {
            pointer-events: auto;
            cursor: pointer;
        }
    }
}
