











































































































.footer {
  padding-top: 4rem;
  padding-bottom: 3rem;
  background-color: #11151b;
  color: #8f8f8f;
  font-size: 0.875rem;
  line-height: 171%;
  .logo {
    display: block;
    width: 5.8125rem;
    height: 2.5rem;
    margin-bottom: 1.5rem;
  }
  a:not(.with-badge) {
    color: #8f8f8f;
    display: block;

    &:hover {
      color: #fff;
    }

    &.router-link-active {
      color: #1fba7c;
    }
  }
  .with-badge {
    display: block;
    margin-bottom: .75rem;
    height: 2.5rem;

    .badge {
      display: inline-block;
      height: 2.5rem;
      width: 8.45rem;

      &:hover {
        opacity: 0.9;
      }
    }
    &:last-child {
      margin-left: 0;
    }
    &:first-child {
      margin-right: 0;
    }
  }
  .dev,
  .api {
    padding: 0.5rem;
    height: 2.5rem;
    width: 8.45rem;
    background-color: #22282e;
    border-radius: 0.1875rem;
    
    .small {
      height: 100%;
    }
    &:hover {
      opacity: 0.9;
    }
  }
  .api {
    margin-bottom: .75rem;
  }
  ul {
    display: inline-block;
    vertical-align: top;
  }
  ul li.list-title {
    line-height: 257%;
    letter-spacing: -0.01em;
    text-transform: uppercase;
    color: #ffffff;
  }
  .icon {
    margin: auto;
  }
}
