.card:not(:last-child) {
  margin-bottom: $default-padding;
}

.collapse.card,
.card {
  overflow: hidden;
  color: #F5F3F5; 
  min-width: 5rem;
  background: #222831;
  box-shadow: 0px 0px 1px rgba(0, 0, 0, 0.1), 0px 2px 6px rgba(0, 0, 0, 0.1), 0px 10px 20px rgba(0, 0, 0, 0.1);
  border-radius: 2px 2px 10px 2px;
  border-left: 2px solid #2A3038;
  
  &.is-vertical-centered {
    display: flex;
    align-items: center;
    text-align: center;

    .card-content {
      width: 100%;
    }
  }

  &.is-card-widget {
    .card-content {
      padding: 2rem 1rem;
    }
  }

  &.transparent {
    background: none;
    box-shadow: none;
    border: none;
    border-radius: none;

    .card-content {
      padding: 0;

      .label {
        margin-bottom: 0.5rem;
        min-height: 2.5rem;
      }
      .count {
        font-family: Ubuntu;
        font-size: 1rem;
        letter-spacing: 0.02em;

        a {
          margin-left: 0.5rem;
        }
      }
    }
  }

  &.small {
    .card-content {
      padding: $default-padding 1rem;

      .label {
        margin-bottom: 0.5rem;
      }
      .count {
        font-family: Ubuntu;
        font-size: 1rem;
        letter-spacing: 0.02em;
      }
    }
  }

  &.is-address-widget {
    background: #1E2329;
    border-left: none;
    padding: 4rem 0;

    &.without-background {
      background: none;
      box-shadow: none;
      padding: 0;
    }

    .card-content {
      margin: 0 auto;
      
      h3 {
        margin-bottom: 1rem;
      }
      h4 {
        line-height: 2rem;
        margin-bottom: 1.5rem;

        .svg-icon {
          width: 2rem;
          height: 2rem;
          margin-right: 1rem;
        }
      }

      > .columns {
        margin-bottom: .5rem;
      }

      .media {
        border: 1px solid #3B434F;
        border-radius: 2px;
        padding: 1rem;
        line-height: 3rem;

        &:hover {
          border-color: #647287;
        }

        &.is-danger {
          border-color: #dc495a;
        }        

        &.with-margins {
          margin-bottom: 1.5rem;
        }

        .qr {
          position: relative;
          z-index: 2;
          width: 3rem;
          height: 3rem;
          display: block;
          margin: 0;
          padding: 0;
          transition: inherit;
          cursor: pointer;
        }
        .media-content {
          /* word-break: break-all; */
          line-height: 1.5rem;
        }
        .media-right {
          display: flex;
          align-items: center;
          height: 3rem;
        }

        &.with-field {
          line-height: 3.75rem;
          padding: 0 1rem;

          .media-left {
            margin-right: .5rem;
          }
          .media-right {
            position: relative;
            height: auto;
            margin-left: .5rem;

            &:before {
              content: '|';
              padding-right: .75rem;
              display: inline-block;
            }
          }
        }
      }

      .balance {
        position: relative;
        border-radius: 2px;
        padding: 3.125rem 0.25rem;
        z-index: 0;

        &:before {
          content: '';
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          background: radial-gradient(45.01% 113.14% at 0% 3.89%, #303741 0%, #22272E 100%);
          z-index: -1;
        }

        a {
          display: inline-block;
          margin-top: 1rem;

          .icon {
            padding-left: .5rem;
          }
        }
      }
      .input,
      .textarea,
      .textarea:focus {
        background-color: transparent !important;
        border: none !important;
        box-shadow: none !important;
        overflow: hidden !important;
        padding: 0;
        outline: none !important;
        color: #F5F3F5;

        &::placeholder {
          color: #647287;
          opacity: .35;
        }
        &:-ms-input-placeholder {
          color: #647287;
        }
        &::-ms-input-placeholder {
          color: #647287;
        }
      }

      .textarea {
        height: 3rem;
      }

      .input {
        height: 3.7rem;
        font-size: 1.125rem;
        text-align: center
      }
    }
  }


  .label {
    font-weight: 300;
    font-size: 1rem;
    line-height: 125%;
    letter-spacing: 0.02em;
    color: #6B7381;
    margin-bottom: 1rem;
  }
  .count {
    font-family: PT Mono;
    font-style: normal;
    font-weight: normal;
    font-size: 1.5rem;
    line-height: 125%;
    color: #F5F3F5;

    .value {
      word-break: break-all;
    }
  }

}

@include tablet {
  .card {  
    &.is-address-widget {
      padding: 1.5rem;
    }
  }
}