/* Theme style (colors & sizes) */
@import "theme-default";

/* Core Libs & Lib configs */
@import "libs/all";

/* Mixins */
@import "mixins";

/* Theme components */
@import "card";
@import "tabs";
@import "table";

/* From main app */

html {
  background-color: #22272e;
  overflow: auto;
}

body {
  font-family: Ubuntu, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #ffffff;
  letter-spacing: 0.02em;
  font-weight: 300;
}

.address,
.hash {
  font-family: 'PT Mono';
}

.icon .mdi-24px.mdi-set, .mdi-24px.mdi:before {
  font-size: 1.5rem !important;
}

a {
  color: #0d95e1;
  transition: all 0.25s;
}
a:hover {
  color: #fff;
}

.content h1,
.content h2,
.content h3,
.content h4,
.is-size-1,
.is-size-2,
.is-size-3,
.is-size-4,
.is-size-5,
.is-size-6,
.mono {
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  color: #ffffff;
}
h1,
.title,
.is-size-1 {
  font-size: 3rem;
  line-height: 112%;
  letter-spacing: -0.02em;
}
.is-size-2 {
}
.content h2,
.is-size-3 {
  font-size: 2rem;
  line-height: 112%;
  letter-spacing: -0.01em;
}
.content h3,
.is-size-4 {
  font-size: 1.5rem;
  line-height: 150%;
}
.content h4,
.is-size-5 {
  font-size: 1.125rem !important;
  letter-spacing: 0.02em;
}

p {
  font-style: normal;
  font-weight: 300;
  font-size: 1rem;
  line-height: 150%;
  letter-spacing: 0.02em;
  color: #f5f3f5;
}

.colored-block {
  border: 1px solid #1fba7c;
  border-image-slice: 1;
  border-width: 1px;
  border-image-source: linear-gradient(to left, #1fba7c, #0d95e1);
  padding: 1.125rem 0.75rem;
  text-align: center;
  line-height: 1.25;

  &:not(:last-child) {
    margin-bottom: 0.75rem;
  }
}
.colored-block .small {
  margin-bottom: 0.25rem;
}
.colored-block:hover {
  background-color: rgba(255, 255, 255, 0.02);
}

.search-block {
  position: relative;

  .colored-block {
    position: absolute;
    visibility: hidden;
    right: 0;
    top: 0;
    width: 22rem;
    bottom: 0;
    height: 2.25em;
    background: #252A32;
    border-radius: 2px;
    padding: 0;
    margin: auto 0;

    .input,
    input:hover,
    input:focus {
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      background-color: transparent;
      border: none;
      box-shadow: none;
      outline: none;
      margin: 0;
      padding: .5rem;
      padding-right: 2rem;
      color: #F5F3F5;

      &::placeholder {
        color: #6B7381;
      }
      &:-ms-input-placeholder {
        color: #6B7381;
      }
      &::-ms-input-placeholder {
        color: #6B7381;
      }
    }
  }

  &:hover {
    .colored-block {
      visibility: visible;
    }
  }

  .icon {
    z-index: 1;
    width: 1rem;
    height: 1rem;

    i:before {
      font-size: 1.25rem
    }
  }

  &.relative {
    position: relative;
    display: inline-block;
    width: 100%;
    max-width: 33rem;
    
    .colored-block {
      visibility: visible;
      position: relative;
      opacity: .85;
      width: 100%;
      height: 2.5rem;
      padding-right: 3rem;

      input {
        position: relative;
      }
    }
    .icon {
      position: absolute;
      right: 1rem;
      top: 0;
      bottom: 0;
      margin: auto 0;
    }
  }
}

/* Explorer */

.large-shadow {
  box-shadow: 0px 2.76726px 2.21381px rgba(0, 0, 0, 0.0196802),
    0px 6.6501px 5.32008px rgba(0, 0, 0, 0.0282725),
    0px 12.5216px 10.0172px rgba(0, 0, 0, 0.035),
    0px 22.3363px 17.869px rgba(0, 0, 0, 0.0417275),
    0px 41.7776px 33.4221px rgba(0, 0, 0, 0.0503198),
    0px 100px 80px rgba(0, 0, 0, 0.07);
}

hr {
  width: 5.625rem;
  height: 2px;
  background-image: url('data:image/png;base64,');
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  background-size: contain;
  margin: 0 0 2rem;
  padding: 0;
  display: inline-block;
}

/* Colors */

.is-ultralight {
  color: #6B7381;
}
.has-text-ultralight {
  color: #6B7381 !important;
}

.button.is-link {
  background-color: transparent;
  color: $link;
  border: 1px solid #0D95E1;
  box-sizing: border-box;
  border-radius: 50px;
  height: auto;
  padding: 1rem 2.25rem;

  &.is-small {
    padding: 0.5rem 1rem;
  }
}

.linear-border {
  border: double 1px transparent;
  background-image: linear-gradient(#252a32, #252a32), radial-gradient(circle at top left, #0d95e1, #1fba7c);
  background-origin: border-box;
  background-clip: padding-box, border-box;
  background-color: transparent;
}

.button {
  &.is-primary {
    color: #F5F3F5;
  }
}
.tags {  

  .tag:not(body) {
    border: 1px solid #3B434F;
    padding: 0.5rem;
    height: auto;
    cursor: pointer;

    &:hover,
    &.is-active {
      background: #2A3038;
    }
  }

  .tag:not(:last-child) {
    margin-right: 1rem
  }
}

.pagination-link {
  background: #1E2329;
  color: $button-color;
  border-color: transparent;

  &.is-current {
    border-color: #252A32;
    background-color: #252A32;
  }
}

.pagination-previous, .pagination-next, .pagination-link {
  border-color: transparent;
  color: $base-color;

  &[disabled] {
    background-color: transparent;
    border-color: transparent
  }

  &:hover {
    color: $button-color;
    border-color: #252A32;
  }
}

/* Backgrounds */

.secondary {
  background: #252A32;

  &.with-underline a {
    text-decoration: underline;
  }
  
  a {
    color: #FFFFFF;
    
    &:hover {
      opacity: 0.85;
    }
  }
}

.transactions {
  .secondary:hover {
    background-color: #2A3038;
  }
}

/* Typografy */

h1 {
  
}
.title {
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 2rem;
  line-height: 112%;
  letter-spacing: -0.02em;
  color: #fff;
  margin-bottom: 2rem;
}

h2 {
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 2rem;
  line-height: 112%;
  letter-spacing: -0.01em;
  color: #F5F3F5;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
h3 {
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 1.5rem;
  line-height: 150%;

  color: #F5F3F5;
}
h4 {
  font-family: PT Mono;
  font-style: normal;
  font-weight: normal;
  font-size: 1.125rem;
  line-height: 156%;
  color: #FFFFFF;
}

/* Transactions */

.red,
.green,
.address,
.hash {
  word-break: break-all;
}
.hash {
  max-width: 23.25rem;
}

.red {
  color: #dc495a !important;
}
a.red:hover {
  color: #e26a77 !important;
}
.green {
  color: #1fba7c !important;
}

.icon {
  &.is-active {
    cursor: pointer;

    &:hover {
      opacity: 0.85;
    }
  }
}

input:-webkit-autofill,
input:-webkit-autofill:hover, 
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  caret-color: #F5F3F5;
  border: 1px solid #F5F3F5;
  -webkit-text-fill-color: #F5F3F5;
  -webkit-box-shadow: 0 0 0px 1000px #F5F3F5 inset;
  transition: background-color 5000s ease-in-out 0s;
}

input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

.block-table {
  padding: 1.25rem 0;

  &.secondary td {
    padding: 1rem 2rem;
    &:nth-child(odd) {
      padding-right: 1.5rem;
      min-width: 11rem;
    }
  }
  
  td {
    padding: 0.75rem 0;
    vertical-align: middle;
    
    &:nth-child(odd) {
      color: #6b7381;
    }
  }
}

.b-checkbox {
  margin: 1.5rem 0;
  &:hover {
    color: $base-color;
  }
}

.input:focus,
.taginput .taginput-container.is-focusable:focus,
.textarea:focus,
.select select:focus,
.is-focused.input,
.taginput .is-focused.taginput-container.is-focusable,
.is-focused.textarea,
.select select.is-focused,
.input:active,
.taginput .taginput-container.is-focusable:active,
.textarea:active,
.select select:active,
.is-active.input,
.taginput .is-active.taginput-container.is-focusable,
.is-active.textarea,
.select select.is-active {
  border-color: #1ea571;
  background-color: transparent;
}

.gradient-button[disabled],
.gradient-button {
  position: relative;
  border: double 0.25rem transparent;
  border-radius: 5.1875rem;
  background-image: linear-gradient(#22272e, #22272e),
    radial-gradient(circle at top left, #0d95e1, #1fba7c);
  background-origin: border-box;
  background-clip: padding-box, border-box;
  background-color: transparent;
  cursor: pointer;
  padding: 1rem 2rem;
  display: inline-block;
  color: white;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-appearance: none;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  position: relative;
  vertical-align: top;
  height: auto;

  font-family: Ubuntu;
  font-style: normal;
  font-weight: bold;
  font-size: 1rem;
  line-height: 125%;
  letter-spacing: 0.02em;
  color: #fff;
  text-align: center;
}
.gradient-button:hover {
  color: #fff !important;
  border-color: transparent;
}
.gradient-button.centered {
  margin: auto;
}
.gradient-button:before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: -0.25rem;
  background-color: transparent;
  background-image: radial-gradient(
    37.83% 19.64% at 45.98% 83.04%,
    rgba(31, 186, 124, 0.31) 0%,
    rgba(13, 149, 225, 0) 100%
  );
  box-shadow: 0px 0.3125rem 0.625rem rgba(13, 149, 225, 0.33);
  border-radius: 5.1875rem;
  /*transition: all .5s;*/
  background-position: bottom;
}
.gradient-button:hover:before {
  background-image: radial-gradient(
    91.14% 47.32% at 45.98% 55.36%,
    rgba(31, 186, 124, 0.31) 0%,
    rgba(13, 149, 225, 0) 100%
  );
  box-shadow: 0px 0.3125rem 1.25rem rgba(13, 149, 225, 0.4);
}
.gradient-button.is-small {
  padding: 0.5rem 1rem;
}
.gradient-button[disabled] {
  opacity: .5;
  &:before {
    content: '';
    background: none !important;
    box-shadow: none !important;
  }
}
.buttons {
  a,
  .button,
  .gradient-button {
    margin-bottom: .5rem
  }
  .gradient-button:not(:last-child):not(.is-fullwidth),
  a:not(:last-child):not(.is-fullwidth) {
    margin-right: 1rem;
  }
  &.is-centered:not(.has-addons) .button:not(.is-fullwidth),
  &.is-centered:not(.has-addons) a:not(.is-fullwidth) {
    margin-left: 0.5rem;
    margin-right: 0.5rem;
  }
  .button:last-child {
    margin-right: 0;
  }
  .button:first-child {
    margin-left: 0;
  }
}

.navbar-dropdown.is-boxed {
  background: rgba(34, 39, 46, 0.8);
  border-radius: 2px;

  a {
    &:hover {
      opacity: .85;
    }
  }
}

/* About pages */

.custom-bg {
  background: radial-gradient(450.91% 312.27% at -0.91% 0%, #303741 0%, #22272E 100%);
  border: 1px solid #3B434F;
  padding: 1rem 2rem;
  margin-bottom: 2rem;
}

/* Simple pages */
.hero.is-fullheight:first-child {
  .title {
    margin-bottom: 2rem;
  }
  .subtitle {
    font-size: 1rem;
    line-height: 150%;
    letter-spacing: 0.02em;
    color: #F5F3F5;
    margin-bottom: 3.5rem;
  }
}

/* Subpages */
.hero {
  background-repeat: no-repeat;
  background-size: cover;
  
  &.is-fullheight {
    background-position: top center;
  }

  + widescreen {
    &.is-fullheight {
      background-size: 100% auto;
      background-position: top center;
    }
  }
}

.is-footer {
  margin-top: -2rem;
  padding-top: 0;
  background-repeat: no-repeat;
  background-position: bottom center;
  -webkit-background-size: cover;
  background-size: cover;
  
  .hero-body {
    padding-top: 10.5rem;
    padding-bottom: 6rem;
  }
  .column.is-offset-1,
  .column.is-offset-1-tablet {
    margin-left: 3%;
  }
}

@include tablet {
  .is-footer {  
    margin-top: -4rem;
  }
}

@include desktop {
  .is-footer {  
    margin-top: -6rem;
  }
}

.faq {
  .answers {
    margin-top: 1.1428rem;
    margin-bottom: 6rem;
  }
  .question {
    margin-top: 0;
    padding-bottom: 2rem;
    h2 {
      padding-top: 0;
      margin-top: 0;
    }
    p {
      word-break: break-all;
    }
  }
  .sidebar {
    margin-top: 1.1428rem;
    position: sticky;
    top: 1.1428rem;
  }
  .table-container {
    font-size: 0.75rem;
  }
}

.content {
  .table {
    margin-top: 1rem;
    border-top: 4px solid #6b7381;
    border-bottom: 4px solid #6b7381;
    margin-left: auto;
    margin-right: auto;
  }
  .table th,
  .table td {
    color: #f5f3f5;
    font-weight: 300;
    padding: 0.75rem 1rem;
    border: none;
  }
  .table th {
    background-color: #2a3038;
  }
  .table td {
    padding: 0.875rem 1rem;
  }
  .table tr:nth-child(2n + 3) td {
    background-color: #252a32;
  }

  .table {
    td,
    th {
      text-align: center !important;
    }
    &.left {
      td:first-child,
      th:first-child {
        text-align: left !important;
      }
    }
    td:last-child,
    th:last-child {
      color: #1fba7c;
    }
  }

  .centered {
    th, td {
      text-align: center !important;
      vertical-align: middle;
    }
  }
}

.notices .snackbar {
  .text {
    color: #1fba7c;
  }
}