<template lang="pug">
  section.hero.is-fullheight(
    v-webp:bg="require('@/assets/hero_umi@2x.png')"
    :class="currentPage"
  )
    .hero-head
    .hero-body
      router-view
    .hero-foot
</template>

<script>
import Header from "@/components/Header";
import Footer from "@/components/Footer";
import Socials from "@/components/Socials";
import Cookie from "@/components/Cookie";
import Application from "@/components/Application";
import { mapGetters } from "vuex";
import detectBrowserLanguage from "detect-browser-language";

export default {
  name: "Main",
  components: {
    Header,
    Footer,
    Socials,
    Cookie,
    Application
  },
  computed: {
    ...mapGetters(["cookie", "application", "locale"]),
    currentPage() {
      return this.$router.currentRoute ? this.$router.currentRoute.name : "";
    }
  },
  data() {
    return {
      interval: null
    };
  },
  beforeMount() {
    const locales = ["en", "ru", "es", "zh", "id"];
    let lang = this.locale;
    if (lang && locales.includes(lang)) {
      this.$translate.setLang(lang);
    } else {
      const ussr = [
        "uz",
        "uk",
        "ar",
        "md",
        "tj",
        "am",
        "az",
        "by",
        "ge",
        "kz",
        "kg",
        "lv",
        "lt",
        "tm"
      ];
      let browserLang = detectBrowserLanguage().substring(0, 2);
      lang = locales.includes(browserLang)
        ? browserLang
        : ussr.includes(browserLang)
        ? "ru"
        : "en";
      this.$store.dispatch("changeLocale", lang);
      this.$translate.setLang(lang);
    }
  },
  created() {
    this.$store.dispatch("getRates");
    this.$store.dispatch("fetchStructures");
    this.interval = setInterval(() => {
      this.$store.dispatch("getRates");
      this.$store.dispatch("fetchStructures");
    }, 30000);
  },
  beforeDestroy: function() {
    clearInterval(this.interval);
  }
};
</script>

